.left {
  display: flex;
  flex-direction: column;
  width: 216px;
  margin: 5px;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  padding: 3px;
  background-color: rgb(0 0 0 / 49%);
}
img {
  width: 150px;
  aspect-ratio: 3/2;
  border-image: 1px 1px 1px white;
}
.left > h1 {
  text-align: center;
}
@media only screen and (max-width: 640px) {
  .left {
    height: 40vh;
    width: calc(100% - 10px);
  }
  .left > div {
    display: flex;
    justify-content: center;
  }
}
